@charset "UTF-8";
@import "variables";
@import "../../../sass/func";
.new_recovery_price {
    .location {
        display: none;
        height: pxToRem(80px);
        line-height: pxToRem(80px);
        background: rgba(#FFBF00, 0.2);
        color: #9D704F;
        padding: 0 pxToRem(24px);
        white-space: nowrap;
        .icon_location {
            display: inline-block;
            line-height: pxToRem(42px);
            font-size: pxToRem(28px);
            background-size: contain; // margin-right: pxToRem(20px);
            .icon {
                display: block;
                background: url("../images/newRecoveryPrice/CombinedShape@2x.png") no-repeat;
                background-size: 100% 100%;
                width: pxToRem(26px);
                height: pxToRem(32px);
                line-height: pxToRem(32px);
                margin: pxToRem(3px) pxToRem(5px) 0 0;
                float: left;
            }
        }
        .btn_getLocation {
            font-size: pxToRem(28px);
            display: inline-block;
            width: pxToRem(150px);
            height: pxToRem(50px);
            line-height: pxToRem(50px);
            padding: 0 pxToRem(12px);
            border-radius: pxToRem(208px);
            border: 1px solid #9D704F;
            text-align: center;
            vertical-align: middle;
            box-sizing: border-box;
        }
    }
    .list_content {
        display: flex;
        flex-wrap: wrap;
        justify-content: flex-start;
        padding-top: pxToRem(30px);
        .item_title{
            font-family: PingFangSC-Regular;
            font-size:pxToRem(36px);
            color: #333;
            margin: pxToRem(40px) 0 pxToRem(30px) pxToRem(34px);
            background: url("../images/partner/icon_task.png") no-repeat left;
            padding-left:pxToRem(30px);
            background-size: 10%;
        }
        .col_item{
            width: 100%;
            margin-bottom: pxToRem(40px);
            .list_item {
                box-sizing: border-box;
                text-align: center;
                padding: pxToRem(20px) 0;
                width: 33.333333333333%;
                float: left;
                img {
                    width: pxToRem(64px);
                    height: pxToRem(64px);
                    margin-bottom: pxToRem(24px);
                }
                .item_name {
                    font-family: PingFangSC-Regular;
                    font-size: pxToRem(28px);
                    color: #666666;
                    line-height: pxToRem(28px);
                    margin-bottom: pxToRem(24px);
                }
                .item_price {
                    font-family: PingFangSC-Medium;
                    font-size: pxToRem(28px);
                    color: #333333;
                    line-height: pxToRem(30px);
                }
            }
        }
    }
}

.fonter_cl {
    position: fixed;
    bottom: 0;
    left: 50%;
    margin-left: pxToRem(-375px);
    @include background("../images/newRecoveryPrice/bg.png",
    750px,
    456px);
    text-align: center;
    .txt {
        width: 100%;
        position: absolute;
        bottom: pxToRem(25px);
        p {
            font-size: pxToRem(28px);
            color: #999;
            line-height: pxToRem(50px);
        }
    }
}

